/* Bmi.css */
.center-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #f8f9fa, #e9ecef);
    font-family: 'Arial', sans-serif;
    padding: 20px;
}

.centered-input, .centered-text {
    font-size: 30px;
    text-align: center;
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    border: 1px solid #ced4da;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
}

.centered-input:hover {
    border-color: #80bdff;
    outline: none;
    box-shadow: 0 0 5px rgba(128, 189, 255, 0.5);
}

button.centered-input {
    cursor: pointer;
    background-color: #007bff;
    color: #ffffff;
    border: none;
    transition: background-color 0.3s ease;
}

button.centered-input:hover {
    background-color: #0056b3;
}

.error-message {
    color: red;
    text-align: center;
    margin-bottom: 10px;
    font-weight: bold;
}

.error-text {
    color: red;
    font-size: 14px;
    margin-top: 5px;
}

.centered-table {
    width: 100%;
    max-width: 800px;
    border-collapse: collapse;
    margin-top: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
}

.centered-table th, .centered-table td {
    padding: 10px;
    text-align: center;
}

.centered-table th {
    background-color: #007bff;
    color: #ffffff;
}

.centered-table tr:nth-child(even) {
    background-color: #f2f2f2;
}

.centered-table tr:hover {
    background-color: #e9ecef;
}